html {
  background: rgb(0, 95, 194);
  background: rgba(0, 95, 194, 1);
  height: 100%;
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.noselect {
  -webkit-touch-callout: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.delay-200ms {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}

.delay-470ms {
  -webkit-animation-delay: 0.47s;
  animation-delay: 0.47s;
}

.delay-1300ms {
  -webkit-animation-delay: 1.3s;
  animation-delay: 1.3s;
}

.delay-2500ms {
  -webkit-animation-delay: 2.5s;
  animation-delay: 2.5s;
}
#portfolio-banner {
    height: 25vh;
    background: #005fc2;
}

#banner-title {
    margin-top: 2vh;
    font-size: 14vh;
    margin-bottom: 0px;
}

#banner-subtitle {
    font-size: 4vh;
    margin-bottom: 3vh;
}

#banner-title,
#banner-subtitle {
    font-family: "Amatic SC", "Helvetica Neue", Helvetica, sans-serif;
    color: #e86600;
}

#menu-box-placeholder {
    height: 3vmin;
    background: #ffc800;
}

#menu-bar {
    background: #ffc800;
}

#menu-ul {
    margin-top: 3.5vh;
    margin-left: 2vmin;
}



li::before {
    display: none;
}

#left-menu-item {
    margin-left: 2vmin;
}

.breadcrumb li:first-child a {
    padding-left: 4vmin;
}

button.menu-choice {
    font-family: "Knewave", Arial, "Helvetica Neue", Helvetica, sans-serif;
    -webkit-text-decoration: bold;
            text-decoration: bold;
    color: rgb(245, 136, 154);
    text-align: center;
    margin-left: 15px;
    margin-right: 15px;
    background: #ffc800;
    cursor: pointer;
    border: 0;
    outline: none;
}

button.menu-choice:hover {
    font-family: "Knewave", Arial, "Helvetica Neue", Helvetica, sans-serif;
    color: rgb(43, 43, 43);
    border-radius: 5px;
}

button:disabled {
    font-family: "Knewave", Arial, "Helvetica Neue", Helvetica, sans-serif;
    color: black;
    cursor: not-allowed;
    opacity: 0.5;
}

#page-footer {
    background: #ffc800 !important;
    height: 1vmin;
    bottom: 0;
    position: relative;
    width: 100%;
}

.fa-copyright {
    font-size: 13px;
}

.fa-github,
.linkedin-foot {
    font-size: 22px;
}

@-webkit-keyframes bounceLot {
    from { -webkit-transform: translate3d(0, -1vmax, 0); transform: translate3d(0, -1vmax, 0); }
    to { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

@keyframes bounceLot {
    from { -webkit-transform: translate3d(0, -1vmax, 0); transform: translate3d(0, -1vmax, 0); }
    to { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

.linkedin-foot {
    -webkit-animation: bounceLot 0.7s cubic-bezier(.5,0.05,1,.5);
            animation: bounceLot 0.7s cubic-bezier(.5,0.05,1,.5);;
    -webkit-animation-direction: alternate;;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes bounceLittle {
    from { -webkit-transform: translate3d(0, -1vmax, 0); transform: translate3d(0, -1vmax, 0); }
    to { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

@keyframes bounceLittle {
    from { -webkit-transform: translate3d(0, -1vmax, 0); transform: translate3d(0, -1vmax, 0); }
    to { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }
}

.fa-github {
    -webkit-animation: bounceLittle 1s cubic-bezier(.5,0.05,1,.5);
            animation: bounceLittle 1s cubic-bezier(.5,0.05,1,.5);;
    -webkit-animation-direction: alternate;;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}


.showcase {
    height: 710px;
    width: 300px;
    margin: 50px auto 0 auto;
}

.project-content {
    height: 470px;
}

.project-links {
    align-items: center;
    display: flex;
    flex-basis: 0;
    flex-grow: 1;
    flex-shrink: 0;
    justify-content: center;
    padding: .75rem;
}

.short-showcase {
    height: 600px;
}

.short-showcase .project-content {
    height: 350px !important;
}

.showcase img {
    border-bottom: 1px solid black;
}

#gh-icon {
    font-size: 3vh;
}

#ext-link-icon {
    font-size: 2.4vh;
}

@media (min-width: 1200px) {
    .showcase {
        height: 574px;
        width: 90vw;
    }
    .project-content {
        height: 400px;
    }
    .showcase img {
        border-right: 1px solid black;
    }
    .footer-links {
        border-left: 1px solid rgba(128, 128, 128, 0.493);
    }
    
}

@media (min-width: 769px) and (max-width: 916px) {
    .showcase {
        height: 750px;
    }
    .project-content {
        height: 550px;
    }

    .short-showcase {
        height: 600px;
    }
}
@media (min-width: 1200px) {
  .card .media:not(:last-child) {
    margin-bottom: 0.75rem;
  }

  .card.is-horizontal {
    display: flex;
    flex-basis: 50ex;
    flex-grow: 0;
    flex-shrink: 1;
  }

  .card-image {
    flex: 4 1;
    flex-shrink: 1;
  }

  .image {
    display: block;
    position: relative;
    overflow: hidden;
    height: 400px;
    width: 100%;
  }

  img.project-img {
    height: 400px;
    object-fit: contain;
    object-position: contain;
  }

  .card-content {
    flex: 3 1;
  }
}

